import { Box, AppBar, styled } from "@mui/material"
import { Colors } from "../theme/index"
export const Wrapper = styled(Box)(({ theme }) => ({
  marginTop: "3.6rem",
  padding: "3.6rem",
}))
export const styles = {
  techTitle: {
    fontSize: "2.4rem",
    position: "relative",
    "&::before": {
      content: '""',
      height: "1.8rem",
      top: "40%",
      width: "15rem",
      position: "absolute",
      backgroundColor: Colors.secondary,
      opacity: "0.7",
      filter: "blur(0.5rem)",
      zIndex: "-1",
    },
  },

  items: {
    textAlign: "center",
    display: "flex",
    flexDirection: "column",
    gap: "3.2rem",
    h2: {
      fontSize: "2.8rem",
    },
  },

  icons: (theme) => ({
    display: "grid",
    gridTemplateColumns: "1fr 1fr 1fr 1fr",
    alignItems: "center",
    justifyItems: "center",
    gap: "1rem",
    mt: "1.6rem",

    img: {
      width: "clamp(3rem, 40%, 5rem)",
    },
    filter: "grayscale(0.5)",

    [theme.breakpoints.down("sm")]: {
      gridTemplateColumns: "1fr 1fr",
    },
  }),

  icons2: {
    mt: "2.4rem",
    display: "flex",
    justifyContent: "center",
    gap: "6rem",
    filter: "grayscale(0.5)",
    img: {
      width: "clamp(3rem, 40%, 5rem)",
    },
  },
}
