import React from "react"
import { Box, Typography, Card, CardMedia } from "@mui/material"
import { Wrapper, styles } from "../styles/about"
import Photo from "../assets/images/ahmad.svg"
const About = () => {
  return (
    <Wrapper>
      <Box sx={styles.textBox}>
        <Typography sx={styles.aboutTitle} variant="h2">
          About me
        </Typography>
        <Typography variant="body1" sx={styles.desc}>
          Im 25 years old and I love coding
        </Typography>

        <Typography variant="body1" sx={styles.desc}>
          I graduated from the Soutern City Colleges, with a bachelor's <br />
          degree in computer science. And worked in a construction firm for 2
          years as safety officer.
        </Typography>
      </Box>
      <img src={Photo} alt="Photo ko" />
    </Wrapper>
  )
}

export default About
