import { Box, styled } from "@mui/material"
import { Colors } from "../theme"

export const Wrapper = styled(Box)(({ theme }) => ({
  marginTop: "2.4rem",
  padding: "3.2rem",
  position: "relative",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  gap: "1.6rem",
  [theme.breakpoints.down("md")]: {
    flexDirection: "column",
    img: {
      width: "80%",
    },
  },
}))

export const styles = {
  aboutTitle: {
    position: "relative",
    fontWeight: "400",
    opacity: "0.99",
    fontSize: "2.8rem",
    zIndex: "999",
    "&::before": {
      content: '""',
      height: "1.8rem",
      top: "35%",
      width: "15rem",
      position: "absolute",
      backgroundColor: Colors.secondary,
      opacity: "0.7",
      filter: "blur(0.5rem)",
      zIndex: "-1",
    },
  },

  desc: {
    fontSize: "1.3rem",
    mt: "1.6rem",
  },
}
