import React from "react"
import { styles, Wrapper } from "../../styles/appbar"
import { Links } from "../../assets/dummydata/index"
import { Link } from "react-router-dom"
import { Box } from "@mui/material"
import Logo from "../../assets/images/Circle.svg"
const Appbar = () => {
  return (
    <Wrapper>
      <Box sx={styles.linksContainer}>
        <a href="/portfolio">
          <img src={Logo} alt="logo image" />
        </a>
        {Links.map((l, index) => {
          const { id, name, path } = l
          return (
            <Link key={id} to={path} sx={styles.li}>
              {name}
            </Link>
          )
        })}
      </Box>
    </Wrapper>
  )
}

export default Appbar
