import React from "react"
import { Box, Typography, Card, CardMedia } from "@mui/material"
import { Wrapper, styles } from "../../styles/techs"
import js from "../../assets/images/js.svg"
import react from "../../assets/images/react.svg"
import redux from "../../assets/images/redux.svg"
import git from "../../assets/images/git.svg"
import sass from "../../assets/images/sasss 1.svg"
import mui from "../../assets/images/mui5 1.svg"
import styled from "../../assets/images/styled 1.svg"
import boot from "../../assets/images/bootstrap 1.svg"
import html from "../../assets/images/html 1.svg"
import css from "../../assets/images/css 1.svg"
const Techs = () => {
  return (
    <Wrapper>
      <Typography variant="h2" sx={styles.techTitle}>
        What about my skills:
      </Typography>
      <Box sx={styles.items}>
        <Box sx={{ mt: "5.4rem" }}>
          <Typography variant="h2">Familiar</Typography>
          <Box sx={styles.icons}>
            <img src={js} />
            <img src={react} />
            <img src={redux} />
            <img src={git} />
            <img src={sass} />
            <img src={mui} />
            <img src={styled} />
            <img src={boot} />
          </Box>
        </Box>
        <Box sx={{ mt: "5.4rem" }}>
          <Typography variant="h2">Confident</Typography>
          <Box sx={styles.icons2}>
            <img src={html} />
            <img src={css} />
          </Box>
        </Box>
      </Box>
    </Wrapper>
  )
}

export default Techs
