import React from "react"
import { Box, Typography } from "@mui/material"
import { Wrapper, styles } from "../styles/home"
import About from "./About"
import Techs from "../components/techs/Techs"
const Home = () => {
  let textPath = `<textPath xlink:href="#curve"> Hello! I'm Ahmad Rethauddin I. Ismael,I'm a web dev. kuno...looking
  for a job</textPath>`

  return (
    <>
      <Wrapper>
        <Typography variant="body1" sx={styles.subhead}>
          <br />
          blah blah blah blah blah blah blah blah blah blah blah blah blah blah
          blah blah blah blah blah blah blah blah blah blah blah blah
        </Typography>
        <svg viewBox="-5 80 360 280">
          <path
            id="curve"
            className="curve"
            d="M 8 64 C 0 56 -17 288 73 241 C 615 -29 97 391 243 409 c 87 -3 88 -1 234 -4"
          />
          <text x="25" dangerouslySetInnerHTML={{ __html: textPath }}></text>;
        </svg>
        <Box sx={styles.blur}></Box>
        <Box sx={styles.sub2Box}>
          <Typography variant="body1" sx={styles.subhead2}>
            I seek to create light, aesthetic and minimalistic websites that
            solve user problems through effective and proven solutions.
          </Typography>
        </Box>
      </Wrapper>
      <About />
      <Techs />
    </>
  )
}
export default Home

